<template>
	<div id="mapContainer"></div>
</template>

<script>
import "leaflet/dist/leaflet.css";
import L from "leaflet";
//import data from "../assets/alle_plekjes.geojson";

// function addToolTip(feature, layer) {
// 	if (feature.properties && feature.properties.name) {
// 		layer.bindTooltip(feature.properties.name);
// 	}
// }

export default {
	name: "LeafletMap",
	data() {
		return {
			center: [51.11, 4.46],
		};
	},
	methods: {
		setupLeafletMap: function () {
			this.map = L.map("mapContainer", {
				center: this.center,
				crs: L.CRS.EPSG3857,
				zoom: 11,
				zoomControl: true,
				preferCanvas: false,
			});
			L.tileLayer(
				"https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
			).addTo(this.map);
		},
	},
	mounted() {
		this.setupLeafletMap();

		//L.geoJSON(data,{onEachFeature: addToolTip}).addTo(this.map)
		//console.log(data)
	},
	beforeUnmount() {
		if (this.map) {
			this.map.remove();
		}
	},
};
</script>

<style scoped>
#mapContainer {
	width: 100vw;
	height: calc(100% - 48px);
	position: absolute;
	top: 48px;
}
